<template>
<el-row>
  <el-col :span="4" v-for="(item, index) in 15" :key=item :offset="index > 0 ? 1 : 0">
    <el-card :body-style="{ padding: '0px' }">
      
      <div style="padding: 14px;">
        <span>好吃的汉堡</span>
        <div>
    <!-- imgBaseUrl为图片URL-->
    <!-- <img v-if="imgBaseUrl"
         style="width:100%"
         :src="imgBaseUrl"
         imBaseUrl="../assets/logo.png"  
         @click.self="showBigImage(imgBaseUrl)"> -->
 
    <img
        @click.self="showBigImage('../assets/logo.png')" style="width:100%"
        src="~@/assets/logo.png"
    />
    <BigImg :visible="photoVisible" :url="bigImgUrl" @closeClick="()=>{photoVisible=false}"></BigImg>
  </div>
        
        <div class="bottom clearfix">
          <div>123</div>
          <div>456</div>
          <el-button type="text" class="button">操作按钮</el-button>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row>
</template>
<script>
import BigImg from"./BigImg"
export default {
  data() {
    
    return {
      photoVisible: false,
      bigImgUrl: ""
    };
  },
  components:{
    BigImg
  },
  methods: {
     
    showBigImage(e) {//点击图片函数，点击后，把photoVisible设置成true
      if (e != "") {
        this.photoVisible = true;
        this.bigImgUrl = e;
        alert(this.bigImgUrl)
      }
    },
}
}
</script>
<style>
  .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>