<template>
    <div v-show="visible" @click="closeClick" class="showPhoto">
      <img class="img" :src="url" alt="图片加载失败" />
    </div>
  </template>
  
  <script>
    export default {
      props: {
        url: {
          type: String,
          default: "",
        },
        visible: {
          type: Boolean,
          default: false,
        },
      },
      methods: {
        closeClick() {
          //子组件可以使用 $emit 触发父组件的自定义事件
          this.$emit("closeClick");
          
        },
      },
    };
  </script>
  <style lang="css" scoped>
    .showPhoto {
      position: fixed;
      top: 0;
      left: 0;
      width: 60%;
      height: 60%;
      background: rgba(0, 0, 0, 0.5);
      z-index: 99999;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 50%; /* 将顶部定位到屏幕中间 */
  left: 50%; /* 将左侧定位到屏幕中间 */
  transform: translate(-50%, -50%); /* 使用 transform 属性来使弹窗居中 */
    }
  
    .showPhoto .img {
      display: block;
      margin: auto 0;
      max-width: 20%;
      text-align: center;
    }
  </style>